@mixin visually-hidden {
  position: absolute;

  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;

  white-space: nowrap;

  border: 0;

  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}


@mixin behavior($first-shadow-behavior: $main-color, $second-shadow-behavior: $third-color) {
  &:hover {
    opacity: 0.6;
  }

  &:focus-visible {
    outline: none;
    box-shadow:
      0 0 0 4px $first-shadow-behavior,
      0 0 0 6px $second-shadow-behavior;
  }

  &:active {
    opacity: 0.3;
  }
}

